// GameDetail.js
import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Box, Typography, Button } from "@mui/material";
import { getGameById } from "../services/gameService"; // Import hm ly game theo ID

const GameDetail = () => {
  const { id } = useParams(); // Ly ID t URL
  const [game, setGame] = useState(null);
  const [error, setError] = useState(null); // Trng thi li
  const navigate = useNavigate();

  useEffect(() => {
    const fetchGame = async () => {
      try {
        const data = await getGameById(id); // Gi API  ly thng tin game
        if (data) {
          setGame(data);
        } else {
          setError("Khng tm thy tr chi. Vui lng th li."); // X l li nu khng tm thy game
        }
      } catch (err) {
        setError("C li xy ra. Vui lng th li sau.");
      }
    };
    fetchGame();
  }, [id]);

  // Nu c li
  if (error) {
    return (
      <Box sx={{ textAlign: "center", marginTop: "50px" }}>
        <Typography variant="h4" color="error">
          {error}
        </Typography>
        <Button
          variant="contained"
          sx={{
            marginTop: "20px",
            background: "#ff5722",
            "&:hover": { background: "#d84315" },
          }}
          onClick={() => navigate(-1)} // Quay li trang trc
        >
          Quay li
        </Button>
      </Box>
    );
  }

  // Nu game cha c ti
  if (!game) {
    return (
      <Box sx={{ textAlign: "center", marginTop: "50px" }}>
        <Typography variant="h4">ang ti...</Typography>
      </Box>
    );
  }

  // Nu game  c thng tin
  return (
    <Box sx={{ padding: "20px", maxWidth: "800px", margin: "auto" }}>
      <Typography variant="h3" sx={{ fontWeight: "bold", textAlign: "center" }}>
        {game.title}
      </Typography>
      <Box
        component="img"
        src={game.image}
        alt={game.title}
        sx={{
          width: "100%",
          height: "auto",
          margin: "20px 0",
          borderRadius: "10px",
          boxShadow: "0 8px 25px rgba(0,0,0,0.15)",
        }}
      />
      <Typography
        variant="body1"
        sx={{ lineHeight: 1.8, marginBottom: "20px" }}
      >
        {game.description || "Khng c m t cho tr chi ny."}
      </Typography>
      <Button
        variant="contained"
        sx={{
          background: "linear-gradient(to right, #ff7e5f, #feb47b)",
          "&:hover": { background: "#ff5722" },
          color: "#fff",
          fontWeight: "bold",
          marginRight: "10px",
        }}
        onClick={() => navigate(-1)} // Quay li trang trc
      >
        Quay li
      </Button>
      <Button
        variant="contained"
        sx={{
          background: "linear-gradient(to right, #6a11cb, #2575fc)",
          "&:hover": { background: "#4a91f2" },
          color: "#fff",
          fontWeight: "bold",
        }}
        onClick={() => window.open(game.downloadUrl, "_blank")} // M link ti
      >
        Ti xung
      </Button>
    </Box>
  );
};

export default GameDetail;
