// Hm ly thng tin chi tit game theo ID
export const getGameById = async (id) => {
  const games = await getGames(); // Ly tt c game
  return games.find(game => game.id === parseInt(id)); // Tm game theo ID
};

const GameList = () => {
  const [games, setGames] = useState([]);

  useEffect(() => {
    const fetchGames = async () => {
      const gameData = await getGames();
      setGames(gameData);
    };
    fetchGames();
  }, []);

  return (
    <Box sx={{ padding: 3, backgroundColor: "#f5f5f5" }}>
      <Typography
        variant="h4"
        sx={{
          textAlign: "center",
          marginBottom: 4,
          fontWeight: "bold",
          color: "#333",
        }}
      >
        Featured Games
      </Typography>

      <Grid container spacing={3} justifyContent="center">
        {games.map((game) => (
          <Grid item key={game.id} xs={12} sm={6} md={4} lg={3}>
            <Card
              sx={{
                borderRadius: 2,
                overflow: "hidden",
                boxShadow: 3,
                "&:hover": {
                  transform: "scale(1.05)",
                  boxShadow: 6,
                },
                transition: "transform 0.3s ease, box-shadow 0.3s ease",
              }}
            >
              <CardMedia
                component="img"
                alt={game.title}
                height="200"
                image={game.image}
                sx={{ objectFit: "cover" }}
              />
              <CardContent>
                <Typography
                  variant="h6"
                  sx={{ fontWeight: "bold", marginBottom: 1 }}
                >
                  {game.title}
                </Typography>
                <Typography
                  variant="body2"
                  color="textSecondary"
                  sx={{ marginBottom: 2 }}
                >
                  {game.description}
                </Typography>
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  sx={{
                    borderRadius: "20px",
                    textTransform: "none",
                    fontSize: "0.9rem",
                    "&:hover": {
                      backgroundColor: "#1976d2",
                    },
                  }}
                >
                  Play Now
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default GameList;
