import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import Home from "./pages/Home";
import About from "./pages/About";
import Login from "./pages/Login";
import SignUp from "./pages/SignUp";
import DownloadPage from "./pages/DownloadPage";
import GameDetail from "./pages/GameDetail"; // Import trang chi tit
import { getGames } from "./services/gameService"; // Dch v  ly game

const App = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false); // Trng thi ng nhp
  const [users, setUsers] = useState([]); // Danh sch ngi dng  ng k
  const [userEmail, setUserEmail] = useState(""); // Email ngi dng
  const [games, setGames] = useState([]); // Danh sch game

  useEffect(() => {
    const fetchGames = async () => {
      const data = await getGames(); // Ly danh sch game t dch v
      setGames(data);
    };
    fetchGames();
  }, []);

  const handleSignUp = (newUser) => {
    setUsers([...users, newUser]);
  };

  const handleLogin = (email) => {
    setIsLoggedIn(true);
    setUserEmail(email); // Lu email ca ngi dng
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
    setUserEmail(""); // Xa email khi ng xut
  };

  return (
    <Router>
      <Navbar isLoggedIn={isLoggedIn} onLogout={handleLogout} />
      <Routes>
        {/* Home ch truy cp khi  ng nhp */}
        <Route
          path="/"
          element={
            isLoggedIn ? (
              <Home userEmail={userEmail} games={games} /> // Truyn games vo Home
            ) : (
              <Navigate to="/login" />
            )
          }
        />
        {/* About ch truy cp khi  ng nhp */}
        <Route
          path="/about"
          element={isLoggedIn ? <About /> : <Navigate to="/login" />}
        />
        {/* Trang login */}
        <Route
          path="/login"
          element={<Login onLoginSuccess={handleLogin} users={users} />}
        />
        {/* Trang ng k */}
        <Route path="/signup" element={<SignUp onSignUp={handleSignUp} />} />
        {/* Trang ti game */}
        <Route
          path="/download/:gameId"
          element={<DownloadPage games={games} />}
        />
        {/* Trang chi tit game */}
        <Route
          path="/game/:id"
          element={<GameDetail games={games} />} // Truyn danh sch game vo GameDetail
        />
      </Routes>
      <Footer />
    </Router>
  );
};

export default App;
